/* Color palette sourced from https://www.color-hex.com/color-palette/114926 Soft Primaries Color Palette palette */
:root {
  --sunset: #b4343f;
  --mustard: #e0c02b;
  --mid-blue: #466da5;
  --pale-grey: #91a8ca;
  --moss-green: #4e6d4a;
  --dark-grey: #2d3235;
  --black: #000;
  --white: #fff;
}

body {
  margin: 0;
  font-family: 'Dosis', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

div {
  margin: 0;
  font-family: 'Dosis', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.contrast {
  background-color: var(--pale-grey);
}

code {
  font-family: 'Dosis', sans-serif;
}

h1 {
  font-size: 60px;
  line-height: 1.2;
  color: var(--mid-blue);
  background-color: var(--white);
  padding-left: 25px;
}

h2,
.card-title {
  font-size: 40px;
  line-height: 1.5;
  color: var(--sunset);
  padding-left: 25px;
}

h3 {
  font-size: 20px;
  line-height: 2;
  color: var(--white);
  background-color: var(--mid-blue);
  padding-left: 25px;
}

li {
  font-size: 25px;
  color: var(--white);
  padding-left: 10px;
}

h4 {
  color: var(--mid-blue);
  background-color: var(--white);
  font-size: 18px;
  line-height: 2.0;
  padding-left: 25px;
}

p {
  font-size: 15px;
  color: var(--mid-blue);
  padding-left: 25px;
}

.child {
  padding: 10px;
}

.contact {
  font-size: 20px;
  color: var(--white);
  padding-left: 2px;
}

.fa {
  position: relative;
  padding-left: 5px;
}

.regex-img {
  margin-left: 20px;
  border-radius: 10px;
}